.about_wraper {
  height: 200vh;
}
.heroe {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: 100vh;
}

.heroe h1 {
  position: relative;
  color: #fffdfd;
  font-size: 25vh;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 25vh;
  text-align: left;
  z-index: 10;
  margin-top: 122px;
}

.heroe h1::after {
  position: absolute;
  content: "";
  width: 55vh;
  height: 55vh;
  top: -40%;
  left: 0%;
  border-radius: 50%;
  background-color: #f50000;
  z-index: -1;
}
.heroe img {
  display: block;
  align-self: flex-end;
  width: 30%;
  margin-right: 3rem;
}

.omnie {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding-left: 1rem;
  width: 90%;

  background-color: #fffdfd;
  box-shadow: #f50000 4rem 4rem;
}
.omnie h1 {
  position: relative;
  text-align: left;
  color: #f50000;
  font-size: 10rem;
  left: 0;
  line-height: 7rem;
  top: -5rem;
  margin: 0;
}

.omnie h3 {
  text-align: left;
  margin: 0;
  color: #09359a;
  font-size: 2rem;
  margin-bottom: 3rem;
}
.omnie p {
  text-align: left;
  margin: 0;
  margin-bottom: 1.5rem;
  color: #25242e;
  padding: 0 2rem 1rem 0;
}
